<template>
  <v-layout row justify-center>
    
    <v-form class="pa-5">
      <h2>Login</h2>
      <v-text-field
        v-model="email"
        label="e-mail"
        placeholder="Correo Electr贸nico"
        outlined
      ></v-text-field>
      <v-text-field
        v-model="password"
        label="e-mail"
        placeholder="Correo Electr贸nico"
        type="password"
        outlined
      ></v-text-field>
      <v-btn color="warning" @click="login"> Enviar </v-btn>
      <!-- <button class="v-btn" color="primary" type="submit" value="acceder">Submit</button> -->
    </v-form>
    <p>{{ mensaje }}</p>
  </v-layout>
    
  
</template>

<script>
import firebase from "firebase";

export default {
  data() {
    return {
      email: "",
      password: "",
      mensaje: "",
    };
  },
  methods: {
    login() {
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(
          (user) => this.$router.replace("account"),
          (error) => alert("hubo un error") /* this.mensaje = error */
        );
    },
  },
};
</script>
